@import 'variables';

* {
  color: $mainText-color;
  font-family: 'Monda', sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size: 20px;

}

body {
  background-color: $bg-color;
}

.container {
  width: 1280px;
  height: 100vh;
  margin: 0 auto;

  .header {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 100px;
    font-size: 20px;

    &__logo {
      font-weight: 700;
      font-size: 72px;

    }

    .credentials {
      margin-top: 15px;
      text-decoration: none;
      position: relative;
      @include glowing-effect(
                      linear-gradient(45deg, #77228a, #d026c7, #840f94, #8b25a8, #500093, #bf00ff, #bf43f3, #ff00c8, #9c5cc7),
                      0px
      );
    }
  }

  .fill__block {
    position: relative;
    display: flex;
    justify-content: center;
    width: fit-content;
    margin: 58px auto;

    input {
      width: 660px;
      color: $mainText-color;
      background-color: $base-color;
      height: 68.56px;
      border: none;
      border-radius: 30px;
      padding-left: 20px;
    }
    &-button {
      position: absolute;
      width: 110.43px;
      height: 68.56px;
      right: 0;
      top: 0;
      border: 2px solid $mainBorder-color;
      border-radius: 0 30px 30px 0;
      cursor: pointer;
      background-color: $bg-color;
      transition: 0.2s ease-in;

      @include glowing-effect(
                      linear-gradient(45deg, #77228a, #d026c7, #840f94, #8b25a8, #500093, #bf00ff, #bf43f3, #ff00c8, #9c5cc7),
                      10px
      );
    }
    &-info {
      width: 666px;
      height: 60px;
      background-color: $base-color;
      margin: 0 auto;
      border: 2px solid #AD02FE;
      display: flex;
      justify-content: space-around;
      align-items: center;
      &__itemsNumber {

      }
      &__button {
       background-color: transparent;
        border: none;
        cursor: pointer;
        &:hover {
         color: $secondText-color;
        }
      }
    }

  }

  .to__doBlock {
    width: 666px;

    height: fit-content;
    background-color: $base-color;
    margin: 0 auto;
    &-block {

      display: flex;
      align-items: center;
      position: relative;
      padding: 18px 20px;
      border: 3px solid $secondBorder-color;

      gap: 30px;



      &__icon {
        height: 32px;
        width: 32px;
        border: 2px solid $mainBorder-color;
        background-color: transparent;
        border-radius: 50px;
        cursor: pointer;
        &:hover {
          background: linear-gradient(111.35deg, #E600FA 10.12%, rgba(82, 97, 234, 0) 82.22%);

        }

        @include glowing-effect(
                        linear-gradient(45deg, #77228a, #d026c7, #840f94, #8b25a8, #500093, #bf00ff, #bf43f3, #ff00c8, #9c5cc7),
                        10px
        );
        &-done {
          background: linear-gradient(111.35deg, #721680 30%, rgba(82, 97, 234, 0) 100.22%);
          cursor: crosshair;
          border: none;
        }
      }

      &__text {
        &-done {
          color: $secondText-color;
          text-decoration: line-through;
          cursor: not-allowed;
        }
      }
    }
  }
}